import React from 'react';

const Carousel = () => (
  <div>
    <div id="myCarousel" className="carousel slide slide-height">
      {/* Carousel */}
      <ol className="carousel-indicators">
        <li data-target="#myCarousel" data-slide-to="0" className="active"></li>
        <li data-target="#myCarousel" data-slide-to="1"></li>
        <li data-target="#myCarousel" data-slide-to="2"></li>
      </ol>
      {/* Carousel */}
      <div className="carousel-inner slide-height">
        <div className="item active">
          <img src="https://anti.dingdingblog.com/images/static/carousel1.png" alt="First slide" />
        </div>
        <div className="item">
          <img src="https://anti.dingdingblog.com/images/static/carousel2.jpg" alt="Second slide" />
        </div>
        <div className="item">
          <img src="https://anti.dingdingblog.com/images/static/carousel1.png" alt="Third slide" />
        </div>
      </div>
      {/* Carousel */}
      <a className="carousel-control left" href="#myCarousel"
        data-slide="prev"><span className="rsaquo">&lsaquo;</span></a>
      <a className="carousel-control right" href="#myCarousel"
        data-slide="next"><span className="rsaquo">&rsaquo;</span></a>
    </div>

    <article className="panel panel-default panel-no-radius">
      <div className="panel-body">
        <h4 style={{ color: '#666' }}>
          <b style={{ color: '#50bff3' }}></b>
          !
        </h4>

        <p className="">
          ,,
          ,
          ,
          
        </p>
      </div>
    </article>
  </div>
)
export { Carousel }



