import React from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import autoBind from 'react-autobind';
import {
  Modal,
  ModalHeader,
  ModalClose,
  ModalBody,
  ModalFooter
} from 'react-modal-bootstrap';
import { auth, getAuth } from '../../functions/auth.js';
import { api } from '../../functions/api.js';

class Navbar extends React.Component {

  constructor(props) {
    super(props);
    autoBind(this);
    this.state = { isOpen: false, islogin: false, username: localStorage.getItem("name") };
    auth(this);
  };

  /**
   * navbar
   * @param {*} nextProps 
   */
  componentWillReceiveProps(nextProps) {
    let width = document.documentElement.clientWidth;
    if (width < 768) {
      //reactdom
      //dom
      //navbarbug
      document.getElementById("bs-example-navbar-collapse-1").className = "collapse navbar-collapse";

    }
  }

  // 
  openModal = () => {
    this.setState({
      isOpen: true
    });
  };

  // 
  hideModal = () => {
    this.setState({
      isOpen: false
    });
  };

  search = () => {
    let keyword = document.getElementById('keyword').value;
    let reg = RegExp(/[\/#]+/);
    if (keyword === '') {
      alert('');
    } else if (reg.test(keyword)) {
      alert('');
    } else {
      this.hideModal()
      window.location.href = '/article/search/' + keyword;
    }

  };

  logout() {
    let auth = getAuth('auth/logout');
    axios.post(api('auth/logout'), {
      id: auth.id,
      sign: auth.sign,
      timestamp: auth.timestamp,
    }).then((response) => {
      if (response.data.code === 200) {
        localStorage.clear();
        this.setState({ islogin: false })
        if (window.location.pathname !== '/') {
          window.location.href = '/';
        }
      } else {
        alert(response.data);
      }
    }).catch((error) => {
      console.log(error);
    });
  }

  renderLogin() {
    if (this.state.islogin) {
      return (
        <li className="dropdown">
          <a className="dropdown-toggle" data-toggle="dropdown" role="button">
            {this.state.username}<span className="caret"></span>
          </a>
          <ul className="dropdown-menu" role="menu">
            <li><Link to="/auth/home"></Link></li>
            <li><a onClick={this.logout}></a></li>
          </ul>
        </li>
      );
    } else {
      return (
        <li><Link to="/login"></Link></li>
      );
    }
  }

  renderRegister() {
    if (this.state.islogin) {

    } else {
      return (
        <li><Link to="/register"></Link></li>
      );
    }
  }

  render() {
    return (
      <div>
        <nav className="navbar navbar-default navbar-no-radius">
          <div className="container">
            <div className="navbar-header">
              <button type="button" className="navbar-toggle collapsed" data-toggle="collapse"
                data-target="#bs-example-navbar-collapse-1">
                <span className="sr-only">Toggle Navigation</span>
                <span className="icon-bar"></span>
                <span className="icon-bar"></span>
                <span className="icon-bar"></span>
              </button>
              <a className="navbar-brand theme-color" href="/">
                <img src="https://anti.dingdingblog.com/images/static/logo.png"
                  className="logo" alt="logo" />
                
              </a>
            </div>

            <div className="collapse navbar-collapse" id="bs-example-navbar-collapse-1">

              <ul className="nav navbar-nav">

              </ul>
              <ul className="nav navbar-nav navbar-right">
                <li><Link to="/"></Link></li>
                <li className="dropdown">
                  <a className="dropdown-toggle" data-toggle="dropdown" role="button">
                    <span className="caret"></span>
                  </a>
                  <ul className="dropdown-menu" role="menu">
                    <li><Link to="/article/tag/1"></Link></li>
                    <li><Link to="/article/tag/2"></Link></li>
                    <li><Link to="/article/tag/3"></Link></li>
                  </ul>
                </li>

                {/* <li><Link to="/"></Link></li> */}
                <li><Link to="/contribution"></Link></li>
                <li><Link to="/notice/show/1"></Link></li>
                <li><Link to="#" onClick={this.openModal}></Link></li>
                {this.renderLogin()}
                {this.renderRegister()}
              </ul>
            </div>
          </div>
        </nav>

        <Modal isOpen={this.state.isOpen} onRequestHide={this.hideModal}>
          <ModalHeader>
            <ModalClose onClick={this.hideModal} />
          </ModalHeader>
          <ModalBody>
            <input name="keyword" id="keyword" className="form-control" />
          </ModalBody>
          <ModalFooter>
            <button className='btn btn-default' onClick={this.hideModal}></button>
            <button className='btn btn-primary' onClick={this.search}></button>
          </ModalFooter>
        </Modal>
      </div>
    )
  }
}

export { Navbar }



